import React from 'react'
import { isInstaUser } from '../../../../../utils';
import Author from '../../../Elements/Author/Author';
import Time from '../../../Elements/Time/Time';
import Network from '../../../Elements/Network/Network';
import Seprator from '../../../Elements/Seprator/Seprator';

  const AuthorDetails = ({ Post, CardStyle, inView }) => {
  const { author, networkId } = Post
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const authorStatus = (CardStyle.author.status && !isInstaUser(author.name)) ? true : false;
  const sepratorStatus = (authorStatus && CardStyle.timeStatus) ? true : false;
  return <div className="tb_px_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className="tb_px_author">
      {authorStatus ? 
          <Author author={author} networkId={networkId} authorClass={'tb_px_author_profile'} inView={inView} />
      : ''}
      <div className="tb_px_author_info">
        {authorStatus ? <div className="tb_px_authorname">{author.name}</div> : ''}
        <div className="tb_px_post_info">
          {authorStatus ? <div className="tb_px_username">{username}</div> : ''}
          {sepratorStatus ? <Seprator sepClass={`tb_px_seprator`} /> : ''}
          {CardStyle.timeStatus ? 
              <Time createdAt={Post.createdAt} timeClass={`tb_px_time tb-cTBfont-${CardStyle.author.variant}`} />
          : ''}
        </div>
      </div>
    </div>
    <div className="tb_px_social_">
      <Network networkClass={'tb_px_social_ico'} networkId={networkId} iconStyle={CardStyle.icon} />
    </div>
  </div>
}
export default AuthorDetails