import React from 'react'
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import ShareOnHover from "../../../Elements/ShareOnHover/ShareOnHover";
import InViewWrapper from '../../../Elements/InViewWrapper';
import { isSocialWall } from '../../../../../actions/api';


const Card = ({ postSize, clickToShowPopUp, index, ThemeStyle, CardStyle, Post, ThemeInfo }) => {
  const mediaType = Post.type !== 1;
  const padding = isSocialWall ? { padding: ThemeStyle.spacing } : null
  const postTextContent =  Post.content.text;

  return (
    <InViewWrapper>
      {({ ref, inView }) => (
        <div ref={ref} style={{ width: `${postSize}%`, padding: CardStyle.style.padding / 2, opacity: inView ? 1 : 0, transition: inView ? 'opacity .5s ease-in-out' : '' }} className={`tb_px_post_wrapper`} role="article" area-label={Post.content.text} post-id={Post.referenceId ? Post.referenceId : Post.id} feed-id={Post.feedId}>
          <div id={`tb_w_c_id-${Post.id}`} tb-network={Post.networkId} post-id={Post.referenceId || Post.id} feed-id={Post.feedId} tabIndex="0" area-label={postTextContent} role="article">
              <div className="tb_px_post_in" onClick={clickToShowPopUp(index, Post)} role="button" aria-label="open detail modal for post" tabIndex="0" style={padding}>
                {Post.share.status ? (
                  <ShareOnHover share={Post.share} shareClass={"tb_w_c_share_container"} Post={Post} ThemeInfo={ThemeInfo} />
                ) : null}
                {mediaType ? (
                  <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={index} inView={inView} />
                ) : null}
                <div className="tb_px_contant_wrapper">
                  <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} mediaType={mediaType} ThemeInfo={ThemeInfo} inView={inView} />
                </div>
              </div>
            </div>
        </div>
      )}
    </InViewWrapper>
  );
}
export default Card