import React, { PureComponent } from "react";
import Masonry from 'react-masonry-component';
import "./scss/Pixel.scss";
import Card from './Elements/Card';
import { getColumns, getSizeInPercent } from "../../../../utils";
class Pixel extends PureComponent {

  componentDidMount(){
    this.props.loadBranding()
  }

  render() {
    const { postData, completeDataObject, CardStyle, ThemeStyle, ThemeInfo, clickToShowPopUp, onClickToCTA, onClickPopUpSlider, containerSize } = this.props;
    const responsiveColumns = Math.max((containerSize.width / (CardStyle.minimumWidth + 20)).toFixed(), 1)
    const columns =  getColumns(containerSize.width, ThemeStyle.column, responsiveColumns);
    const postSize = getSizeInPercent(containerSize.width, columns)

    return (<Masonry
                className={`tb_px_post_container`}
                tabIndex='0'
                aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
                elementType={'div'}
                options={{
                  transitionDuration: 0,
                  percentPosition: true,
                  itemSelector: '.tb_px_post_wrapper',
                  horizontalOrder: false,
                  resize: true,
                }}
                disableImagesLoaded={false}
                updateOnEachImageLoad={false}
                imagesLoadedOptions={{}}
                enableResizableChildren={true}>{
                  ((postData && postData.length > 0)) && postData.map((item, index) => {
                    const cardData = completeDataObject[item];
                    return <Card key={index} postSize={postSize} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} Post={cardData} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo}/>
                  })
                }
              </Masonry>
    );
  }
}

export default Pixel