import React from "react";
import { isTagShop, isSocialEmbed } from "../../../../../actions/api";
import Image from '../../../Elements/Media/Image';
import Video from '../../../Elements/Media/Video';
import MediaIcon from '../../../Elements/MediaIcon/MediaIcon';
import Like from '../../../Elements/Reactions/Like';

const Media = ({ Post, ThemeStyle, index, inView, wallID }) => {
  const mediaIcon = Post.hotspot ? false : true
  const isVideo = ([3,5].includes(Post.type)) && (ThemeStyle.autoPlay == 1) ? true : false
  const interactive = ThemeStyle.interactive && isSocialEmbed ? true : false
  return <div className="tb_px_media_wrap">
      <MediaIcon Post={Post} IconClass={`tb_px_media_icon`} show={mediaIcon} hideVideo={ThemeStyle.autoPlay} />
      {isVideo ? 
      <Video VideoClass="tb_px_video" Post={Post} size={false} isCover={true} controls={false} autoPlay={true} muted={true} observer={true} index={index} inView={inView} /> 
      :
      <Image ImageClass="tb_px_image" Post={Post} size={100} hotspot={false} blurBG={isTagShop} observer={true} index={index} inView={inView} /> }
      {interactive ? <Like Post={Post} ThemeStyle={ThemeStyle} wallID={wallID} interactiveCount={ThemeStyle.interactiveCount} /> : null}
  </div>
}
export default Media